"use client";

import React, { useEffect, useRef, useState } from "react";
import * as fabric from "fabric";
import * as pdfjsLib from "pdfjs-dist";
import "pdfjs-dist/web/pdf_viewer.css";

pdfjsLib.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjsLib.version}/pdf.worker.min.js`;

interface PdfCanvasProps {
  pdfFile: File | null;
  currentPage: number;
  onTotalPages: (total: number) => void;
  toolMode: "select" | "text" | "draw" | "rect";
  color: string;
  canvasRef?: React.MutableRefObject<fabric.Canvas | null>; // 匯出畫布用
}

const PdfCanvas: React.FC<PdfCanvasProps> = ({
  pdfFile,
  currentPage,
  onTotalPages,
  toolMode,
  color,
  canvasRef,
}) => {
  const canvasDomRef = useRef<HTMLCanvasElement>(null);
  const fabricRef = useRef<fabric.Canvas | null>(null);
  const [pdfDoc, setPdfDoc] = useState<pdfjsLib.PDFDocumentProxy | null>(null);

  // 載入 PDF 文件
  useEffect(() => {
    if (!pdfFile) return;

    const reader = new FileReader();
    reader.onload = async () => {
      const data = new Uint8Array(reader.result as ArrayBuffer);
      const doc = await pdfjsLib.getDocument({ data }).promise;
      setPdfDoc(doc);
      onTotalPages(doc.numPages);
    };
    reader.readAsArrayBuffer(pdfFile);
  }, [pdfFile]);

  // 渲染當前頁面
  useEffect(() => {
    if (!pdfDoc || !canvasDomRef.current) return;

    const renderPage = async () => {
      const page = await pdfDoc.getPage(currentPage);
      const viewport = page.getViewport({ scale: 1.5 });
      const canvasEl = canvasDomRef.current!;
      canvasEl.width = viewport.width;
      canvasEl.height = viewport.height;

      const context = canvasEl.getContext("2d")!;
      await page.render({ canvasContext: context, viewport }).promise;

      // 建立 Fabric 畫布
      if (fabricRef.current) fabricRef.current.dispose();
      const fabricCanvas = new fabric.Canvas(canvasEl, {
        selection: true,
        backgroundColor: null,
      });
      fabricCanvas.setWidth(viewport.width);
      fabricCanvas.setHeight(viewport.height);
      fabricRef.current = fabricCanvas;

      // 對外傳出 canvas 實體
      if (canvasRef) {
        canvasRef.current = fabricCanvas;
      }
    };

    renderPage();
  }, [pdfDoc, currentPage]);

  // 工具模式切換（文字、筆刷、矩形、選取）
  useEffect(() => {
    const canvas = fabricRef.current;
    if (!canvas) return;

    canvas.isDrawingMode = false;
    canvas.selection = false;
    canvas.defaultCursor = "default";

    if (toolMode === "draw") {
      canvas.isDrawingMode = true;
      canvas.freeDrawingBrush = new fabric.PencilBrush(canvas);
      canvas.freeDrawingBrush.color = color;
      canvas.freeDrawingBrush.width = 2;
    } else if (toolMode === "select") {
      canvas.selection = true;
      canvas.defaultCursor = "move";
    } else if (toolMode === "text") {
      canvas.defaultCursor = "text";
    } else if (toolMode === "rect") {
      canvas.defaultCursor = "crosshair";
    }
  }, [toolMode, color]);

  // 點擊畫布建立物件
  useEffect(() => {
    const canvas = fabricRef.current;
    if (!canvas) return;

    const handleClick = (event: fabric.IEvent) => {
      const pointer = canvas.getPointer(event.e);

      if (toolMode === "text") {
        const text = new fabric.IText("輸入文字", {
          left: pointer.x,
          top: pointer.y,
          fill: color,
          fontSize: 20,
        });
        canvas.add(text).setActiveObject(text);
      } else if (toolMode === "rect") {
        const rect = new fabric.Rect({
          left: pointer.x,
          top: pointer.y,
          width: 100,
          height: 60,
          fill: "transparent",
          stroke: color,
          strokeWidth: 2,
        });
        canvas.add(rect).setActiveObject(rect);
      }
    };

    canvas.on("mouse:down", handleClick);
    return () => {
      canvas.off("mouse:down", handleClick);
    };
  }, [toolMode, color]);

  return (
    <div className="flex justify-center p-4 bg-white">
      <canvas ref={canvasDomRef} />
    </div>
  );
};

export default PdfCanvas;
